.feedback {
	padding-bottom: 80px;
	@include sm {
		padding-bottom: 30px; }
	&__title {
		text-align: center;
		margin-bottom: 80px;
		@include sm {
			margin-bottom: 20px; } }
	&__gallery {
		font-size: 0;
		margin: 0 -15px; }
	&__item {
		display: inline-block;
		padding: 0 15px;
		width: 33.33%;
		position: relative;
		vertical-align: top;
		@include sm {
			display: block;
			width: 100%;
			&:not(:last-child) {
				margin-bottom: 15px; } } }
	&__description {
		font-size: 17px;
		line-height: 25px;
		color: $white;
		background: $dodger-blue;
		padding: 40px 36px 45px 62px;
		letter-spacing: -0.02em;
		border-radius: 4px;
		position: relative;
		margin-bottom: 15px;
		@include sm {
			padding: 20px 18px 20px 30px;
			font-size: 14px;
			line-height: 20px;
			margin-bottom: 10px; }
		&:after {
			content: '';
			position: absolute;
			left: 35px;
			bottom: -12px;
			@include arr(30,16, $dodger-blue, b);
			transform: rotate( 12deg);
			@include sm {
				bottom: -6px;
				@include arr(15,8, $dodger-blue, b); } }
		&:before {
			content: attr(data-symbol);
			position: absolute;
			top: 25px;
			left: 25px;
			font-size: 75px;
			font-family: $familyGeorgia;
			line-height: 1;
			color: $dark-blue;
			@include sm {
				top: 12px;
				left: 12px;
				font-size: 37px; } } }
	&__info {
		padding: 0 8px; }
	&__client {
		font-weight: 400;
		color: $night-blue;
		font-size: 16px;
		line-height: 21px;
		margin-bottom: 7px;
		@include sm {
			font-size: 13px;
			margin-bottom: 5px; } }
	&__login {
		font-weight: 400;
		color: $light-gray;
		font-size: 14px;
		line-height: 23px;
		@include sm {
			font-size: 12px; } } }

